body {
  background-color: #171717;
  color: #FFFFFF;
  font-family: 'Titillium Web', sans-serif;
  margin: 0;
  padding: 0;
  overflow-x: hidden; /* Enable horizontal scrolling */
  overflow-y: auto;
}

.home-page {
  padding: 20px;
  padding-top: 80px; /* Adjust top padding to account for fixed search bar */
}

.search-bar-container {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  padding: 10px 20px;
  background-color: #171717;
  z-index: 1000;
}

.search-bar-wrapper {
  position: relative;
  width: 100%;
}

.search-bar {
  width: 93%;
  padding: 10px 40px;
  margin-bottom: 0; /* Adjust margin as needed */
  border: none;
  border-radius: 4px;
  background-color: #282828;
  color: #FFFFFF;
  font-size: 16px;
}

.back-button,
.search-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
  height: 24px;
  cursor: pointer;
}

.back-button {
  left: 10px;
}

.search-button {
  right: 57px;
}

.suggestions-list {
  position: absolute;
  top: 60px; 
  width: 100%;
  background-color: #282828;
  border: 1px solid #444;
  border-radius: 4px;
  max-height: 200px;
  overflow-y: auto;
  z-index: 10;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.suggestions-list li {
  padding: 10px;
  cursor: pointer;
  color: #FFFFFF;
}

.suggestions-list li:hover,
.suggestions-list .highlight {
  background-color: #444;
}

.content-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
}

.content-card {
  background-color: #282828;
  padding: 10px;
  border-radius: 5px;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.content-card img {
  width: 100%;
  height: auto;
  aspect-ratio: 2 / 3;
}

.name-container {
  position: relative;
}

.truncated-name {
  margin: 10px 0 0 0;
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}

.truncated-name:hover::after,
.truncated-name:focus::after,
.truncated-name:active::after {
  content: attr(title);
  position: absolute;
  left: 50%;
  transform: translate(-50%, -110%);
  background-color: rgba(0, 0, 0, 0.8);
  color: #FFFFFF;
  padding: 5px 10px;
  border-radius: 4px;
  white-space: nowrap;
  z-index: 10;
}

.truncated-name:hover::before,
.truncated-name:focus::before,
.truncated-name:active::before {
  content: '';
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 110%;
  border-width: 5px;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.8) transparent transparent transparent;
}

.loader {
  text-align: center;
  margin-top: 20px;
  font-size: 16px;
}
